import { graphql } from "gatsby"
import Layout from "src/components/layout"
import FullPost from "src/components/post/full"
import { Node, PostFrontmatter } from "src/utils/types"
import { React } from "src/utils/view"

export default function Post({ data }: { data: any }) {
  const markdownRemark: Node<PostFrontmatter> = data.markdownRemark
  const { frontmatter, htmlAst, timeToRead } = markdownRemark
  return (
    <Layout>
      <FullPost
        frontmatter={frontmatter}
        htmlAst={htmlAst}
        timeToRead={timeToRead}
      />
    </Layout>
  )
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { url: { eq: $path } }) {
      htmlAst
      timeToRead
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        url
        title
        author
        categories
        tags
        type
        version
      }
    }
  }
`
